export default "/**\r\n *\r\n * 你好。我叫汤利俊。从事前端开发工作。\r\n *\r\n * 我喜欢研究一些前端有趣的交互。\r\n *\r\n * 要不来一场直播写代码？\r\n */\r\n\r\n/**\r\n * 那么开始吧，首先让一切都动起来~\r\n */\r\n\r\n* {\r\n  -webkit-transition: all .5s;\r\n}\r\n\r\n/**\r\n * 这看似没用呀，别急，一会儿你就会看到效果了。\r\n *\r\n * 我们来做一些变化。\r\n */\r\n\r\nhtml {\r\n  background: #336699;\r\n}\r\n\r\n/***\r\n * 等等。\r\n */\r\n\r\npre {\r\n  color: #ccc;\r\n}\r\n\r\n/**\r\n * 看上去好多了。但是有点刺眼了。\r\n *\r\n * 而且在整个屏幕上写代码有点浪费空间了。\r\n *\r\n * 我来做一些优化。\r\n */\r\n\r\npre:not(:empty) {\r\n  overflow: auto;\r\n  background: #333;\r\n  height: 44.6%;\r\n  width: 50%;\r\n  font-family: Arial;\r\n  padding: 10px 10px 20px;\r\n  white-space: pre-wrap;\r\n  outline: 0;\r\n}\r\n\r\n/**\r\n * 接下来我们在把它移到屏幕右侧。\r\n */\r\n\r\n#style-text {\r\n  -webkit-transform: translateX(90%);\r\n  position: absolute;\r\n}\r\n\r\n/**\r\n * 看上去还不错喔, 但是所有的字体颜色是单色的！\r\n *\r\n * 那么，我们来把代码变的更加可读吧~\r\n */\r\n\r\n.comment {\r\n  color: #888;\r\n  font-style: italic;\r\n}\r\n.selector {\r\n  color: #ff6600;\r\n}\r\n.key {\r\n  color: #ffff99;\r\n}\r\n.value {\r\n  color: #99cccc;\r\n}\r\n.value.px {\r\n  color: #99cccc;\r\n}\r\n\r\n/**\r\n * 现在我们有了一些进展。\r\n *\r\n * 再把它变得更立体一些。\r\n */\r\n\r\nbody {\r\n  -webkit-perspective: 1000px;\r\n}\r\n\r\n#style-text {\r\n  -webkit-transform: translateX(98.5%) rotateY(-10deg);\r\n  -webkit-transform-origin: right;\r\n  height: 94.5%;\r\n}\r\n\r\n/**\r\n * 好吧差不多了，接下来就聊聊我吧。\r\n *\r\n * 我相信你应该不是来只看我炫技的吧？\r\n */\r\n\r\npre:not(#style-text) {\r\n  -webkit-transform: rotateY(10deg);\r\n  -webkit-transform-origin: left;\r\n}\r\n"